    let globalData = getApp().globalData;
    let app = getApp().$vm;
    let utils = require('@/util/utils.js');
    let api = utils.api;
    let request = utils.request;
    export default {
        data() {
            return {
                type: 1,
                oldPhone: "",
                newPhone: "",
                oldPwd: '',
                newPwd: '',
                confPwd: ''
            }
        },
        methods: {
            confirmSelectPhone: function() {
                let _this = this;
                this.oldPhone = app.trim(this.oldPhone)
                this.newPhone = app.trim(this.newPhone)
                if (this.oldPhone == "") {
                    uni.showToast({
                        icon: "none",
                        title: "请填写原手机号"
                    })
                    return;
                }
                if (this.newPhone == "") {
                    uni.showToast({
                        icon: "none",
                        title: "请填写新手机号"
                    })
                    return;
                }
                let data = {
                    userid: uni.getStorageSync('userinfo').userid,
					
                    oldmobile: _this.oldPhone,
                    newmobile: _this.newPhone
                }
				console.log("手机号修改",data)
                api.revisePhoneNumber(data)
                    .then(data => {
                        let [err, res] = data;
                        console.log("手机号修改:", res)
                        if (res.data.success) {
                            uni.showModal({
                                content: "手机号修改成功",
                                showCancel: false,
                                success: () => {
                                    uni.navigateBack({
                                        delta: 1
                                    })
                                }
                            })
                        } else {
                            uni.showModal({
                                content: res.data.errmsg,
                                showCancel: false
                            })
                        }
                    })
            },
            confirmSelectPwd: function() {
                let _this = this;
                this.oldPwd = app.trim(this.oldPwd)
                this.newPwd = app.trim(this.newPwd)
                this.confPwd = app.trim(this.confPwd)
                if (this.oldPwd == '') {
                    uni.showToast({
                        icon: "none",
                        title: "请填写原密码"
                    })
                    return;
                }
                if (this.newPwd == '') {
                    uni.showToast({
                        icon: "none",
                        title: "请填写新密码"
                    })
                    return;
                }
                if (this.newPwd != this.confPwd) {
                    uni.showToast({
                        icon: "none",
                        title: "两次密码输入不一致！"
                    })
                    return;
                }
                let data = {
                    userid: uni.getStorageSync('userinfo').userid,
                    oldpwd: this.oldPwd,
                    newpwd: this.confPwd
                }
				console.log("密码修改",data)
                api.revisePwd(data)
                    .then(data => {
                        let [err, res] = data;
                        console.log(res)
                        if (res.data.success) {
                            uni.showModal({
                                content: "密码修改成功",
                                showCancel: false,
                                success: () => {
                                    uni.navigateBack({
                                        delta: 1
                                    })
                                }
                            })
                        } else {
                            uni.showModal({
                                content: res.data.errmsg,
                                showCancel: false
                            })
                        }
                    })
            }
        },
        onLoad: function(option) {
            this.type = option.type;
            console.log(option)
        }
    }
